h2 {
  font-size: 2rem;
}

.resumeBody {
  width: 66%;
  margin: 0 auto;
}

.contents {
  display: flex;
}

.resumeContainer {
  background: black;
  min-height: 100vh;
  width: 100%;
  color: white;
  font-family: "Roboto Mono", monospace;
}

.resumeHeader {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 100%;
}

.headerExpander {
  flex-grow: 1;
}

.resumeText {
  font-size: 3rem;
  margin: auto 1rem;
}

.langSection {
  padding-left: 2rem;
  position: relative;
}

.viewPDF {
  /* margin: auto 1rem; */
  margin: 1rem;
  font-size: 1rem;
  position: fixed;
  bottom: 0;
  right: 0;
}

.flex-med {
  display: flex;
}

.flex-med h2 {
  margin: auto 0;
  line-height: 100%;
}

.expLevel {
  height: 1.5rem;
  width: 10rem;
  border-color: var(--primary);
  border-width: 2px;
  border-style: solid;
  margin: auto 1rem;
  padding: 0;
}

.expFill {
  background: var(--secondary);
  background: linear-gradient(
    90deg,
    var(--secondary) 0%,
    var(--secondary-offset) 100%
  );
  height: 100%;
  position: relative;
  font-size: 1rem;
  text-align: left;
  vertical-align: middle;
}

.expText {
  height: 100%;
}

.textBody {
  text-indent: 2rem;
}

h2 {
  background: var(--sunrise);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  width: min-content;
}

.projectImage {
  min-width: 10%;
  max-width: 7rem;
  padding-bottom: 10%;
  height: 0;
  background-size: cover;
  z-index: 2;
  align-self: center;
}

.projCard {
  position: relative;
  --padding: 0.5rem;
  width: 100%;
  min-height: 7rem;
  display: flex;
  padding: var(--padding);
  background: var(--sunrise);
  border: none;
}

.projCard::before {
  --border: 2px;
  content: "";
  position: absolute;
  background: black;
  left: var(--border);
  top: var(--border);
  right: var(--border);
  bottom: var(--border);
  /* height: calc(100% - 2 * var(--border));
  width: calc(100% - 2 * var(--border)); */
  z-index: 1;
}

.projBody {
  display: flex;
  flex-direction: column;
  margin-left: 1rem;
  z-index: 2;
  font-size: 1rem;
}

.title {
  font-size: 1.5rem;
}

.indent {
  padding-left: 2rem;
}

h4 {
  position: relative;
  width: fit-content;
  padding-left: 1rem;
}

h4::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 3px;
  height: 100%;
  content: "";
  background: var(--amin);
}
